main {
  background-color: hsl(0, 0%, 100%);
  margin: 0px 8rem;
  box-shadow: 0 0.15rem 1.75rem 0 rgb(33 40 50 / 15%);
}

main .sec-nav .list {
  display: flex;
  list-style: none;
  box-shadow: 0px 2px 5px rgb(133, 127, 127);
  margin-top: 0px;
}
main .sec-nav ul li {
  padding: 20px 10px;
  font-weight: 500;
}
.content-container {
  margin-top: -14px;
  background: linear-gradient(to right, #0062e6, #33aeff);
  border: 1px solid rgb(14, 4, 4);
  height: 105vh;
}
#pack {
  margin-left: -20px;
}
main .price {
  display: flex;
  align-items: center;
  flex-direction: column;
  line-height: 10px;
}
main .heading-1,
.heading-2,
.heading-3 {
  width: 40vh;
  height: 100%;
  margin: 30px 0px 0px 30px;
  gap: 10px;
  background-color: white;
  border-radius: 20px;
  transition: all 0.2s;
}
main .heading-1:hover,
.heading-2:hover,
.heading-3:hover {
  box-shadow: 10px 10px 2px rgb(43, 105, 238);
  transform: translateY(-1%);
}
main .content-container #pack {
  display: flex;
  place-content: center;
}
main .heading-1 ul,
.heading-2 ul,
.heading-3 ul {
  list-style: none;
  margin-left: -15px;
}
main .heading-1 ul li,
.heading-2 ul li,
.heading-3 ul li {
  line-height: 15px;
}
main .heading-1 ul li:before {
  content: "✓ ";
}
.heading-2 ul li::before {
  content: "✓ ";
}
.heading-3 ul li::before {
  content: "✓ ";
}
main .heading-1 ul button,
main .heading-2 ul button,
main .heading-3 ul button {
  font-size: 80%;
  border-radius: 5rem;
  letter-spacing: 0.1rem;
  font-weight: bold;
  /* padding: 1rem; */
  opacity: 0.7;
  background-color: #007bff;
  color: #ffffff;
  padding: 15px 65px;
}
hr {
  width: 85%;
}

/* Html Content Css Start Start from here  */

.Html-Content,
.CSS-Content {
  height: 100vh;
  background-color: rgb(234, 234, 241);
  display: flex;
  place-content: center;
}
.Html-Content h3,
.CSS-Content h3 {
  transform: translateY(45%);
}
