navbar {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
}
.Nav-container {
  display: flex;
  height: 90px;
  background-color: white;
  align-items: center;
  justify-content: space-evenly;
  opacity: 1;
}
.left-nav {
  height: 100px;
  border-left: 10px;
}
.left-nav ul {
  display: flex;
  align-items: center;
  list-style: none;
}
.left-nav img {
  height: 55px;
  align-items: center;
}
.mid-nav ul {
  position: relative;
  display: flex;
  align-items: center;
  list-style: none;
  margin: auto;
}
.mid-nav ul li,
.right-nav ul li {
  padding: 0px 20px 0px 0px;
  color: #69707a;
  font-weight: 500;
}

.right-nav ul {
  display: flex;
  list-style: none;
  margin: auto;
}
banner {
  display: flex;
  height: 80vh;
  width: 100%;
  background: #f4a100;
  clip-path: polygon(100% 0, 100% 62%, 0 87%, 0 0);
}
/* .banner-div {
  top: 50%;
  left: 50%;
  transform: rotateY(-50%, -50%);
} */
.banner-div {
  display: flex;
  justify-content: space-between;
  margin: 100px 0px 0px 145px;
}
.banner-div h1 {
  font-size: 2.5rem;
  font-weight: 500;
  color: #ffffff;
  letter-spacing: 0.05pc;
}
.banner-div p {
  font-size: 1.15rem;
  color: #ffffff80;
  margin-top: -20px;
  letter-spacing: 0.05pc;
}
.banner-right {
  height: 100px;
  width: 323px;
  padding: 1rem;
  border-radius: 0.5rem;
  overflow: hidden;
  font-size: 0.8rem;
  line-height: 1rem;
  background-color: rgba(242, 246, 252, 0.15);
  box-shadow: 0 0.15rem 1.75rem 0 rgb(33 40 50 / 15%);
  margin: 50px 0px 0px 150px;
}
.banner-right img {
  height: 100px;
}
.banner-right p {
  display: block;
  margin-left: 145px;
  margin-top: -6.5rem;
  color: hsla(0, 0%, 100%, 0.6);
  text-decoration: none;
  font-weight: 700;
  font-size: 0.8rem;
}
footer {
  height: 130vh;
  background-color: rgb(235, 225, 225);
  clip-path: polygon(
    51% 17%,
    80% 16%,
    100% 10%,
    100% 100%,
    78% 100%,
    54% 100%,
    18% 100%,
    0 99%,
    0 22%,
    21% 16%
  );
}
footer .bottom {
  margin: 0 8rem;
  place-content: center;
}

.boy-div {
  display: flex;
  justify-content: space-between;
  padding: 0 7rem;
  background-color: white;
  transform: translateY(50%);
  height: 45vh;
  overflow: hidden;
  box-shadow: 0 0.15rem 1.75rem 0 rgb(33 40 50 / 15%);
}

.boy-div .boy-content {
  justify-content: center;
  transform: translateY(15%);
}
.boy-div .boy-content p {
  color: #69707a;
  font-size: 17px;
}
.boy-div .boy-content p1 {
  display: block;
  font-size: 14px;
}
.boy-div .boy-content h2 {
  color: #363d47;
}
.boy-div .boy-content input {
  padding: 14px 220px 14px 18px;
}
.boy-div .boy-content button {
  background-color: rgb(245, 133, 133);
  padding: 14px 10px 14px 18px;
  color: white;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  cursor: pointer;
}

.article-div {
  display: flex;
  justify-content: center;
  transform: translateY(120%);
}
.article-div .One-div {
  color: #69707a;
  padding: 0px 20px;
}
.article-div .One-div ul li {
  margin: 0px 0px 0px -15px;
  line-height: 25px;
}
.copywright-div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  line-height: 0px;
  transform: translateY(270%);
  color: #69707a;
}
